import axios from "axios";
import authActions from "./auth-actions";

axios.defaults.baseURL = "https://goit-phonebook-api.herokuapp.com";

const token = {
  set(token) {
    axios.defaults.headers.common.Authorization = `Bearer ${token}`;
  },
  unset() {
    axios.defaults.headers.common.Authorization = "";
  },
};

/*
 * POST @ /users/signup
 * body: { name, email, password }
 * ÐŸÐ¾ÑÐ»Ðµ ÑƒÑÐ¿ÐµÑˆÐ½Ð¾Ð¹ Ñ€ÐµÐ³Ð¸ÑÑ‚Ñ€Ð°Ñ†Ð¸Ð¸ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ñ‚Ð¾ÐºÐµÐ½ Ð² HTTP-Ð·Ð°Ð³Ð¾Ð»Ð¾Ð²Ð¾Ðº
 */
const register = (userData) => (dispatch) => {
  // console.log('register');
  dispatch(authActions.registerRequest());

  axios
    .post("/users/signup", userData)
    .then((response) => {
      token.set(response.data.token);
      return dispatch(authActions.registerSuccess(response.data));
    })
    .catch((error) => dispatch(authActions.registerError(error.message)));
};

/*
 * POST @ /users/login
 * body: { email, password }
 * ÐŸÐ¾ÑÐ»Ðµ ÑƒÑÐ¿ÐµÑˆÐ½Ð¾Ð³Ð¾ Ð»Ð¾Ð³Ð¸Ð½Ð° Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ñ‚Ð¾ÐºÐµÐ½ Ð² HTTP-Ð·Ð°Ð³Ð¾Ð»Ð¾Ð²Ð¾Ðº
 */
const login = (userData) => (dispatch) => {
  dispatch(authActions.loginRequest());

  axios
    .post("/users/login", userData)
    .then((response) => {
      token.set(response.data.token);
      return dispatch(authActions.loginSuccess(response.data));
    })

    .catch((error) => dispatch(authActions.loginError(error.message)));
};

/*
 * POST @ /users/logout
 * headers: Authorization: Bearer token
 * ÐŸÐ¾ÑÐ»Ðµ ÑƒÑÐ¿ÐµÑˆÐ½Ð¾Ð³Ð¾ Ð»Ð¾Ð³Ð°ÑƒÑ‚Ð°, ÑƒÐ´Ð°Ð»ÑÐµÐ¼ Ñ‚Ð¾ÐºÐµÐ½ Ð¸Ð· HTTP-Ð·Ð°Ð³Ð¾Ð»Ð¾Ð²ÐºÐ°
 */
const logout = () => (dispatch) => {
  // console.log(logout);
  dispatch(authActions.logoutRequest());

  axios
    .post("users/logout")
    .then((_) => {
      token.unset();
      dispatch(authActions.logoutSuccess());
    })
    .catch((error) => dispatch(authActions.logoutError(error.message)));
};

/*
 * GET @ /users/current
 * headers:
 *    Authorization: Bearer token
 *
 * 1. Ð—Ð°Ð±Ð¸Ñ€Ð°ÐµÐ¼ Ñ‚Ð¾ÐºÐµÐ½ Ð¸Ð· ÑÑ‚ÐµÐ¹Ñ‚Ð° Ñ‡ÐµÑ€ÐµÐ· getState()
 * 2. Ð•ÑÐ»Ð¸ Ñ‚Ð¾ÐºÐµÐ½Ð° Ð½ÐµÑ‚, Ð²Ñ‹Ñ…Ð¾Ð´Ð¸Ð¼ Ð½Ðµ Ð²Ñ‹Ð¿Ð¾Ð»Ð½ÑÑ Ð½Ð¸ÐºÐ°ÐºÐ¸Ñ… Ð¾Ð¿ÐµÑ€Ð°Ñ†Ð¸Ð¹
 * 3. Ð•ÑÐ»Ð¸ Ñ‚Ð¾ÐºÐµÐ½ ÐµÑÑ‚ÑŒ, Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ‚ ÐµÐ³Ð¾ Ð² HTTP-Ð·Ð°Ð³Ð¾Ð»Ð¾Ð²Ð¾Ðº Ð¸ Ð²Ñ‹Ð¿Ð¾Ð»Ð½ÑÐµÐ¼ Ð¾Ð¿ÐµÑ€Ð°Ñ†Ð¸ÑŽ
 */
const getCurrentUser = () => async (dispatch, getState) => {
  const {
    auth: { token: persistedToken },
  } = getState();

  if (!persistedToken) {
    return;
  }

  token.set(persistedToken);
  dispatch(authActions.getCurrentUserRequest());
  try {
    const response = await axios.get("users/current");
    dispatch(authActions.getCurrentUserSuccess(response.data));
  } catch (error) {
    dispatch(authActions.getCurrentUserError(error.message));
  }
};

// eslint-disable-next-line import/no-anonymous-default-export
export default { register, login, logout, getCurrentUser };